<template>
    <div>
        <!-- 降雨量 -->
        <div class="item-pr">
            <h4>
                <span>1</span>
                <strong>降雨量监控</strong>
                <b>更多</b>
            </h4>
            <ul>
                <li class="li-title">
                    雨量计 1
                </li>
                <li>
                    累计雨量
                    <span>(雨量计 1)</span>
                    <b>3.65mm</b>
                </li>
                <li>
                    日雨量
                    <span>(雨量计 1)</span>
                    <b>3.65mm</b>
                </li>
                <li>
                    时雨量
                    <span>(雨量计 1)</span>
                    <b>3.65mm</b>
                </li>
            </ul>
        </div>
        <!-- 位移监控 -->
        <div class="item-pr">
            <h4>
                <span>2</span>
                <strong>位移监控</strong>
            </h4>
            <ul>
                <li>
                    X轴
                    <span>(拉线式位移 1)</span>
                    <b>3.65mm</b>
                </li>
                <li>
                    Y轴
                    <span>(拉线式位移 1)</span>
                    <b>3.65mm</b>
                </li>
            </ul>
        </div>
        <!-- 温湿度监控 -->
        <div class="item-pr">
            <h4>
                <span>3</span>
                <strong>温湿度监控</strong>
            </h4>
            <ul>
                <li>
                    湿度
                    <span>(温湿度计 1)</span>
                    <b>37%</b>
                </li>
                <li>
                    温度
                    <span>(温湿度计 1)</span>
                    <b>3℃</b>
                </li>
            </ul>
        </div>
        <!-- 气象监控 -->
        <div class="item-pr">
            <h4>
                <span>4</span>
                <strong>气象监控</strong>
            </h4>
            <ul>
                <li>
                    风向
                    <span>(风向风速计 1)</span>
                    <b>3.65mm</b>
                </li>
                <li>
                    风速
                    <span>(风向风速计 1)</span>
                    <b>3.65mm</b>
                </li>
                <li>
                    风向
                    <span>(风向风速计 2)</span>
                    <b>3.65mm</b>
                </li>
                <li>
                    风速
                    <span>(风向风速计 2)</span>
                    <b>3.65mm</b>
                </li>
            </ul>
        </div>
        <!-- 表面倾斜监控 -->
        <div class="item-pr">
            <h4>
                <span>5</span>
                <strong>表面倾斜监控</strong>
            </h4>
            <ul>
                <li>
                    X轴
                    <span>(表面倾斜计 1)</span>
                    <b>3.65mm</b>
                </li>
                <li>
                    Y轴
                    <span>(表面倾斜计 1)</span>
                    <b>3.65mm</b>
                </li>
            </ul>
        </div>
    </div>
</template>

<script>
export default {};
</script>

<style lang="scss" rel="stylesheet/scss">
@import "../assets/hcsy.scss";
.item-pr {
    border-radius: rem(10);
    background-color: white;
    font-size: rem(40);
    box-shadow: rem(2) rem(5) rem(20) rem(2) #d4d4d4;
    h4 {
        height: rem(95);
        line-height: rem(95);
        padding: 0 rem(30);
        > span {
            background-color: #cecece;
            padding: rem(1) rem(10);
            color: white;
            height: rem(40);
        }
        strong {
            font-weight: bold;
        }
        b{
            float: right;
            color: #528dff;
        }
    }
    ul {
        .li-title {
            padding-left: rem(30);
        }
        .li-title::before {
            display: none;
        }
        li {
            padding: 0 rem(30);
            display: block;
            border-top: 1px solid $bordercolor;
            height: rem(95);
            line-height: rem(95);
            text-indent: rem(30);
            margin-bottom: rem(35);
            padding-left: rem(70);
            span {
                color: #8d8d8d;
            }
            b {
                float: right;
                color: $color;
            }
        }
        li::before {
            content: "";
            display: block;
            width: rem(10);
            height: rem(10);
            border-radius: 50%;
            background-color: #8d8d8d;
            line-height: rem(95);
            float: left;
            margin-top: rem(42);
        }
    }
}
</style>



